<template>
	<div class="tc">
		<div class="success-tips">
			<svg xmlns="http://www.w3.org/2000/svg"  x="0px" y="0px" width="114.5px" height="114.5px" viewBox="0 0 114.5 114.5" >
			<path id="path1" fill="none" stroke-width="6" stroke-miterlimit="10" d="M96.775,48.604c0.701,3.016,1.072,6.158,1.072,9.389  c0,22.828-18.507,41.334-41.334,41.334c-22.828,0-41.333-18.506-41.333-41.334c0-22.827,18.505-41.333,41.333-41.333  c10.373,0,19.854,3.821,27.111,10.133"/>
			<polyline id="path2"  fill="none" stroke-width="6" stroke-miterlimit="10" points="33.679,58.041 53.929,79.791   102.179,21.541 "/>
			</svg>
			<p>支付成功</p>
			<p>{{time}}s后跳转到原页面</p>
		</div>
	</div>
</template>
<script>
	export default{
		data:function() {
			return {
				time:4
			}
		},
		activated:function() {
			this.time=4;
			var willgo=this.$route.query.from;
			var timer=setInterval(function(){
				if(this.time==0){
					clearInterval(timer);
					this.$router.replace(willgo)
				}else{
					this.time--;
				}
			}.bind(this),1000)

			// const timer = setInterval(() => {
			//     if (!this.sms_countdown) {
			//         clearInterval(timer);
			//     } else {
			//         this.sms_countdown--;
			//         if (this.sms_countdown === 0) {
			//             this.code_msg = '重新获取';
			//             this.isFlag = true;
			//         }
			//     }
			// }, 1000);
			// this.zhuan()

		}
	}
</script>
<style scoped>
	 .success-tips{
	     padding-top:0.3rem;
	     text-align: center;
	}
	 .success-tips p{
	     opacity: 0;
	     -webkit-animation:show 0.2s 0.6s forwards;
	     animation:show 0.2s 0.6s forwards;
	}
	 #path1,#path2 {
	     stroke:#e6292c;
	     fill:none;
	}
	 #path1{
	     stroke-dasharray:300;
	     stroke-dashoffset:-300;
	     -webkit-animation:pathline 0.2s forwards cubic-bezier(0.21, 0.79, 0.54, 0.92);
	     animation:pathline 0.2s forwards  cubic-bezier(0.21, 0.79, 0.54, 0.92);
	}
	 #path2{
	     stroke-dasharray:180;
	     stroke-dashoffset:180;
	     -webkit-animation:pathline 0.4s 0.2s forwards  cubic-bezier(0.91, 0.24, 1, 1);
	     animation:pathline 0.4s 0.2s forwards  cubic-bezier(0.91, 0.24, 1, 1);
	}
	 @-webkit-keyframes pathline{
	     to{
	         stroke-dashoffset:0;
	    }
	}
	 @keyframes pathline{
	     to{
	         stroke-dashoffset:0;
	    }
	}
	 @-webkit-keyframes show{
	     to{
	         opacity: 1;
	    }
	}
	 @keyframes show{
	     to{
	         opacity: 1;
	    }
	}
	p{
		margin: 10px;
	}
	 
</style>


